@mixin no-gutters ($size: 0px) {
    //&.row {
    @include media-breakpoint-up(sm) {
        margin-right: -$size;
        margin-left: -$size;

        & > [class^="col-"],
        & > [class*=" col-"] {
            padding-right: $size;
            padding-left: $size;
        }
    }
    //}
}

//font size - rem. @include font-size(14px)
@function calculateRem($size) {
    $remSize: $size / 16px;
    @return $remSize * 1rem;
}
@mixin font-size($size) {
    font-size: $size;
    font-size: calculateRem($size);
}



